.card {
  width: $card-width;
  padding: 32px 16px 16px;
  border-radius: 5px;
  border: 1px solid $card-border-color;
  background: $color-white;
  transition: color $transition-duration, transform $transition-duration;

  &__img {
    display: block;
    width: 160px;
    min-height: 134px;
    margin: 0 auto 40px;
  }

  &__title {
    margin-bottom: 4px;
    color: $text-color-main;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
  }

  &__code {
    font-size: 10px;
    line-height: 14px;
    margin-bottom: 16px;
    color: $text-color-secondary;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 24px;
  }

  &__reviews {
    font-size: 10px;
    text-align: right;
    line-height: 14px;
    color: $text-color-main;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;

    &-label {
      font-size: 12px;
      line-height: 18px;
      color: $text-color-secondary;
    }

    &-value {
      font-size: 16px;
      font-weight: bold;
      line-height: 18px;
      color: $text-color-main;
    }
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    height: 40px;
    color: $color-white;
    background-color: $blue-accent;
    border: 1px solid $blue-accent;
    font-size: 14px;
    font-weight: bold;
    line-height: 16px;
    text-decoration: none;
    text-transform: uppercase;

    &:hover {
      background-color: $color-white;
      color: $blue-accent;
    }
  }

  &:hover {
    transform: scale(1.2);

    .card__title {
      color: #34568b;
    }
  }
}
